.list__item {
  display: flex;
  justify-content: space-between;
}

.list__text {
  margin: 0;
  padding: 0;
}

.list__button {
  padding: 2px 6px;
  border: 0;
  border-radius: 5px;
  color: #3e3e3e;
  background: #11998e;
  background: -webkit-linear-gradient(to right, #59c173, #11998e);
  background: linear-gradient(to right, #59c173, #11998e);
  cursor: pointer;
  box-shadow: 0px 7px 9px -10px rgba(0, 0, 0, 0.65);
}

@media (min-width: 600px) {
  .list__text {
    font-size: 1.2em;
  }
  .list__button {
    padding: 4px 10px;
    font-size: 1em;
  }
}
